.products_banner {
  margin-top: 80px;
  width: 100%;
  height: 350px;
  background: url("../images/products_banner.jpg") no-repeat center center/cover;

  .products_banner_title {
    font-size: 4rem;
    text-align: center;
    line-height: 350px;
    color: #fff;
    font-weight: 700;
  }
}

.products_category {
  width: 1250px;
  margin: 0 auto;
  display: flex;
  gap: 35px;
  padding: 60px 0;
  li {
    > button {
      background: none;
      border: none;
      font-size: 1rem;
      font-weight: 500;
      cursor: pointer;
      font-family: "Noto Sans KR", sans-serif;
      color: #888;
      transition: 0.5s;
      &:nth-of-type(1).on {
        font-weight: 500;
        color: #fff;
      }

      &:hover {
        color: #000;
      }
    }
  }
}
.productsWrap {
  width: 1250px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 13px;
}

@media all and (max-width: 1024px) and (min-width: 768px) {
  .products_banner {
    .products_banner_title {
      font-size: 3.2rem;
    }
  }
  .products_category {
    width: 100%;
    padding: 60px 20px;
    box-sizing: border-box;
  }
}
@media all and (max-width: 768px) and (min-width: 425px) {
  .products_banner {
    height: 300px;
    .products_banner_title {
      font-size: 2.7rem;
      line-height: 300px;
    }
  }
  .products_category {
    width: 100%;
    padding: 60px 20px;
    box-sizing: border-box;
  }
}
@media all and (max-width: 425px) and (min-width: 320px) {
  .products_banner {
    height: 200px;
    .products_banner_title {
      font-size: 2rem;
      line-height: 200px;
    }
  }
  .products_category {
    gap: 16px;
    width: 100%;
    flex-wrap: wrap;
    padding: 60px 20px;
    box-sizing: border-box;
  }
}
